import React from 'react';

export default function Faqs() {
  return (
    <div className="w-full p-3 flex flex-col">
        <h1 className="font-bold text-[36px] mb-3 text-center">Faqs</h1>

        <ul className="text-xl">
            <li className="p-3"><span className="font-bold mr-2">What is car detailing?</span>Car detailing involves thorough cleaning, restoration, and finishing of a vehicle to achieve a show-quality level of detail. It includes interior and exterior cleaning, paintwork polishing, and protection.</li>
            <li className="p-3"><span className="font-bold mr-2">How often should I have my car detailed?</span>It depends on several factors including driving conditions, vehicle storage, and personal preference. Generally, a full detail is recommended every 6-12 months, with regular washes in between.</li>
            <li className="p-3"><span className="font-bold mr-2">What is the difference between a car wash and car detailing?</span>A car wash is primarily focused on the exterior surface cleaning of the vehicle, often using automated systems or hand washes. Car detailing goes much deeper, including both exterior and interior cleaning, with attention to detail that restores and protects the vehicle.</li>
            <li className="p-3"><span className="font-bold mr-2">How long does car detailing take?</span>The time can vary based on the vehicle's condition and the level of service requested, but typically, a full detail can take between 2 to 8 hours.</li>
            <li className="p-3"><span className="font-bold mr-2">Can detailing remove scratches from my car's paint?</span>Minor scratches and swirl marks can often be removed or greatly diminished through polishing during the detailing process. However, deep scratches that penetrate the paint may require more extensive repairs.</li>
            <li className="p-3"><span className="font-bold mr-2">What kind of products do you use for detailing?</span>All Pro Network detailers use professional-grade, vehicle-safe cleaning products and equipment, including pH-balanced soaps, microfiber towels, and high-quality waxes and sealants to protect your vehicle.</li>
            <li className="p-3"><span className="font-bold mr-2">Do you offer mobile detailing services?</span>Pro Network detailers offer both mobile and in-house services.</li>
            <li className="p-3"><span className="font-bold mr-2">How much does car detailing cost?</span>Prices vary based on the size of the vehicle and the level of detail requested. Basic cleanings can start as low as $50 and full details with long-lasting protection could cost upwards of $3,000.
            </li>
            <li className="p-3"><span className="font-bold mr-2">Can detailing improve the resale value of my car?</span>Absolutely. A well-maintained and detailed car can significantly increase its appeal and potentially its resale value, as it shows the car has been well cared for.</li>
            <li className="p-3"><span className="font-bold mr-2">Is engine bay cleaning included in car detailing?</span>Yes, engine bay cleaning can be included upon request. It involves carefully cleaning the engine bay area to remove dirt, grease, and grime, improving both appearance and functionality.</li>
            <li className="p-3"><span className="font-bold mr-2">What is paint correction?</span>Paint correction is a process that removes imperfections in a vehicle's finish, such as swirls, scratches, oxidation, and etching, restoring the paint to its original shine.</li>
            <li className="p-3"><span className="font-bold mr-2">How can I maintain my car’s detail after service?</span>Regular washing with a pH-neutral soap, avoiding harsh chemicals, using microfiber towels for drying, and applying a quality wax or sealant periodically can help maintain the detail.</li>
            <li className="p-3"><span className="font-bold mr-2">Can you remove pet hair during detailing?</span>Yes, pet hair removal can be included upon request. Pro Netword detailers have specialized tools and techniques to effectively remove pet hair from your vehicle's interior.</li>
            <li className="p-3"><span className="font-bold mr-2">What measures do you take to protect my vehicle during detailing?</span>Pro Networkd detailers use only high-quality, vehicle-safe products, and are trained professionals employing techniques that ensure your vehicle is protected and treated with care throughout the detailing process.</li>
            <li className="p-3"><span className="font-bold mr-2">Do you offer any protective coatings like ceramic or wax?</span>Yes, Pro Network detailers offer various protective coatings, including wax, sealants, and ceramic coatings, each providing different levels of protection and durability for your vehicle’s exterior.</li>
            <li className="p-3"><span className="font-bold mr-2">How do I prepare my vehicle for detailing?</span>We recommend removing personal items and any valuables from your vehicle before the detailing appointment to ensure a thorough and efficient service.</li>
            <li className="p-3"><span className="font-bold mr-2">Can detailing fix faded paint?</span>Detailing can significantly improve the appearance of faded paint through polishing and waxing, but severely faded or damaged paint may require repainting.</li>
            <li className="p-3"><span className="font-bold mr-2">Do you offer headlight restoration?</span>Yes, headlight restoration can be included upon request. It involves cleaning and polishing the headlights to restore clarity and improve visibility.</li>
        </ul>

        <a className="w-3/4 md:w-1/2 lg:w-2/5 mx-auto p-3 bg-gray-800 text-white text-center rounded h-[50px] font-bold" href="/">GET QUOTES</a>
    </div>

  )
};
